import React from 'react';

// Bootstrap
import { Carousel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const titles = [
	"How to Train Your Dragon: The Hidden World",
	"Captain Marvel",
	"Deadpool"];

class PopSlider extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			movies: [],
		}
	}

	shouldComponentUpdate() {
		if (Object.keys(this.state.movies).length === 3) {
			return true;
		}
		return false;
	}

	componentDidMount() {
		var url = "https://9db5che6y2.execute-api.us-east-1.amazonaws.com/Prod/retrieveMovies?type=find_one&title=";
		var movies = {};

		titles.forEach((title) => {
		  // Todo...
		  	console.log(title);
			fetch(url + title)
		      .then(res => res.json())
		      .then(
		        (result) => {
		        	movies[title] = JSON.parse(result.movie);
		        },
		        // Note: it's important to handle errors here
		        // instead of a catch() block so that we don't swallow
		        // exceptions from actual bugs in components.
		        (error) => {
		          alert(error);
		        }
		    ).then(() =>
		    	{this.setState({movies: movies});}
		    );
		});
		
	}

	render() {
		if (this.state.movies.length === 0) return null;
		console.log(this.state.movies[titles[0]])
		const H1 = styled.h1`
			margin: 1em 0 0.5em 0;
			color: #343434;
			font-weight: normal;
			font-family: 'Ultra', sans-serif;   
			font-size: 36px;
			line-height: 42px;
			text-transform: uppercase;
			text-shadow: 0 2px white, 0 3px #777;
		`;
		return (
			<Carousel interval={3000} className="PopSliderCarousel">
			  <Carousel.Item>
			  	<Link to={{ pathname: process.env.PUBLIC_URL + '/movie-page/' + this.state.movies[titles[0]].Title, state: {movieInfo: this.state.movies[titles[0]]} }}>
			    	<img
				      style={{width: "100%", height: "auto", maxWidth: "100%", maxHeight: "50%", display: "block"}}
				      src="https://ws3.sinaimg.cn/large/006tKfTcly1g1bzxnru3zj30vo0akta0.jpg"
				      alt="First slide"
				    />
			    </Link>
			  </Carousel.Item>
			  <Carousel.Item>
			  	<Link to={{ pathname: process.env.PUBLIC_URL + '/movie-page/' + this.state.movies[titles[1]].Title, state: {movieInfo: this.state.movies[titles[1]]} }}>
			    <img
			      style={{width: "100%", height: "auto", maxWidth: "100%", display: "block"}}
			      src="https://ws4.sinaimg.cn/large/006tKfTcly1g1c01564jsj30vo0akwky.jpg"
			      alt="Third slide"
			    />
			    </Link>
			  </Carousel.Item>
			  <Carousel.Item>
			  	<Link to={{ pathname: process.env.PUBLIC_URL + '/movie-page/' + this.state.movies[titles[2]].Title, state: {movieInfo: this.state.movies[titles[2]]} }}>
			    <img
			      style={{width: "100%", height: "423px", maxWidth: "100%", maxHeight: "50%", display: "block"}}
			      src="https://image.tmdb.org/t/p/original/n1y094tVDFATSzkTnFxoGZ1qNsG.jpg"
			      alt="Third slide"
			    />
			    </Link>
			  </Carousel.Item>
			  
			</Carousel>
		);
	}
}

export default PopSlider;