import React from 'react';
import { Switch, Route} from 'react-router-dom';
import Home from './Pages';
import {GenrePage, Page1, MoviePage, SearchPage, TopRated, UserList} from './Pages';
// import {GENRE, ErrorImageSrc, BackgroundImg} from './Constants';

class Main extends React.Component {
	render() {
		return(
			<main style={{marginTop:50}} >
				<Switch>
			      <Route exact path={process.env.PUBLIC_URL+'/'} component={Home} />
			      <Route path={process.env.PUBLIC_URL+'/Recommend'} component={Page1} />
			      <Route path={process.env.PUBLIC_URL+'/movie-page/:title'} render={(props) => <MoviePage 
			      	email={this.props.email} 
			      	isAuthenticated={this.props.isAuthenticated}
			      	{...props} />} />
			      <Route path={process.env.PUBLIC_URL+'/Top'} component={TopRated} />
			      <Route path={process.env.PUBLIC_URL+'/Popular'} component={Page1} />
			      <Route path={process.env.PUBLIC_URL+'/AdvancedSearch'} component={SearchPage} />
			      <Route path={process.env.PUBLIC_URL+'/genre/:type'} render={(props) => <GenrePage pageLimit={24} {...props} />} />
			      <Route path={process.env.PUBLIC_URL+'/user/:type'} render={(props) => <UserList 
			      	email={this.props.email} 
			      	isAuthenticated={this.props.isAuthenticated}
			      	pageLimit={24} {...props} />} />
			    </Switch>
			</main>
		);
	}
}


export default Main

