class MoviePage extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			movie: '',
			isFavorite: false,
			isWatchlist: false,
			rating: 0,
		}
		this.handleFavorite = this.handleFavorite.bind(this);
		this.handleWatchlist = this.handleWatchlist.bind(this);
		this.changeRating = this.changeRating.bind(this);
		this.cancelRated = this.cancelRated.bind(this);
	}

	componentDidMount() {
		const movie = this.props.location.state.movieInfo;

		getImages(movie.imdbID)
		.then(response => this.setState({
			movie: movie,
			poster: "https://image.tmdb.org/t/p/original" + response[0],
			backdrop: "https://image.tmdb.org/t/p/original" + response[1],
		}))
		.catch(error => 
			this.setState({
			movie: movie,
			poster: "https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg",
			backdrop: "https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg",
		}));

		const opts = {"type": "inFavorite", "email": sessionStorage.email, "title": movie.Title};
		getUserList(opts).then(res => {this.setState({isFavorite: res.exist})});

		const opts2 = {"type": "inWatchlist", "email": sessionStorage.email, "title": movie.Title};
		getUserList(opts2).then(res => {this.setState({isWatchlist: res.exist})});

		const opts3 = {"type": "rateMovie", "email": sessionStorage.email, "title": movie.Title, "rate": "0"};
		getUserList(opts3).then(res => {this.setState({rating: res.rate === null ? 0 : res.rate})});

	}

	componentDidUpdate(prevProps) {
		const type = this.props.match.params.title;
		const prevType = prevProps.match.params.title;
		// console.log(type);
		// console.log(prevType);
		// console.log(this.props.location.state.movieInfo);
		if (type !== prevType) {
			const movie = this.props.location.state.movieInfo;

			getImages(movie.imdbID)
			.then(response => this.setState({
				movie: movie,
				poster: "https://image.tmdb.org/t/p/w300" + response[0],
				backdrop: "https://image.tmdb.org/t/p/original" + response[1],
			}))
			.catch(error => 
				{
				this.setState({
				movie: movie,
				poster: "https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg",
				backdrop: "https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg",
			})});
		}

		if (prevProps.isAuthenticated === false && this.props.isAuthenticated === true) {
			this.getRate();
		}

	}

	changeRating( newRating ) {
		const movie = this.props.location.state.movieInfo;
		const opts = {"type": "rateMovie", "email": sessionStorage.email, "title": movie.Title, "rate": newRating};
		getUserList(opts).then(res => {this.setState({rating: newRating})});
    }

    cancelRated() {
    	const movie = this.props.location.state.movieInfo;
    	const opts = {"type": "rateMovie", "email": sessionStorage.email, "title": movie.Title, "rate": "-1"};
		getUserList(opts).then(res => {this.setState({rating: 0})});
    }

	handleFavorite() {
		if (this.state.isFavorite) {
			const opts = {"type": "removeFavorite", "email": sessionStorage.email, "title": this.state.movie.Title}	
			addToUserList(opts).then(() => this.setState({isFavorite: false}));
		}
		else {
			const opts = {"type": "favorite", "email": sessionStorage.email, "title": this.state.movie.Title}
			addToUserList(opts).then(() => this.setState({isFavorite: true}));
		}
		
	}

	handleWatchlist() {
		if (this.state.isWatchlist) {
			const opts = {"type": "removeWatchlist", "email": sessionStorage.email, "title": this.state.movie.Title}	
			addToUserList(opts).then(() => this.setState({isWatchlist: false}));
		}
		else {
			const opts = {"type": "watchlist", "email": sessionStorage.email, "title": this.state.movie.Title}
			addToUserList(opts).then(() => this.setState({isWatchlist: true}));
		}
	}

	getRate() {
		const opts3 = {"type": "rateMovie", "email": sessionStorage.email, "title": this.props.location.state.movieInfo.Title, "rate": "0"};
		getUserList(opts3).then(res => {this.setState({rating: res.rate === null ? 0 : res.rate})});
	}

	render() {
		const JumbotronStyle = styled(Jumbotron)`
			background-color: rgba(1, 1, 1, 0.71) !important;
			background-image: url( ${this.state.backdrop} );
			background-blend-mode: color;
		`;

		const BtnSMMinus = 
			styled(Button)`
				margin-left: 5px;
				width: 30px;
			    height: 30px;
			    padding: 4px 8px;
			    border-radius: 30px;
			    font-size: 12px;
			    line-height: 1.33;
			`;

		const BtnXlFavorite = 
			styled(Button)`
				width: 60px;
			    height: 60px;
			    padding: 8px 16px;
			    border-radius: 30px;
			    font-size: 24px;
			    line-height: 1.33;
			    color: ${!this.state.isFavorite? "white" : "red"}; 
			`
		
		
		const BtnXLWatchlist = 
			styled(Button)`
				width: 60px;
			    height: 60px;
			    padding: 8px 16px;
			    border-radius: 30px;
			    font-size: 24px;
			    line-height: 1.33;
			    color: ${!this.state.isWatchlist? "white" : "red"}; 
			`
		
		return (
			<React.Fragment>
				<JumbotronStyle className="jumbotron text-center">
					<Row> 
						<Col md={4} offset={1} mx={3} my={3}>
					      <div className="view overlay">
					        <img src={this.state.poster} className="img-fluid" alt="">
					        </img>
					      </div>
					    </Col>

					    <Col className="col-md-7 text-md-left ml-3 mt-3">
					      <h1 style={{color:"white"}}>{this.state.movie.Title + " (" + this.state.movie.Year + ")"}</h1>
					      <p style={{color:"white"}} className="font-weight-normal">{this.state.movie.Genre}</p>
					      <Row>
					      	<Col md={2}>
								{this.props.isAuthenticated ? 
									<OverlayTrigger
									    placement="top"
									    overlay={
									        <Tooltip >
									        	{!this.state.isFavorite? "Add to favorite." : "Remove from favorite."}
									        </Tooltip>
									    }
									>
									<BtnXlFavorite variant="outline-light" onClick={this.handleFavorite}> <FaHeart  /> </BtnXlFavorite>
									</OverlayTrigger>
									:
									<OverlayTrigger
									    placement="top"
									    overlay={
									        <Tooltip >
									        	Please sign in first.
									        </Tooltip>
									    }
									>
									<span className="d-inline-block">
						     			<BtnXlFavorite variant="outline-light" style={{pointerEvents: 'none'}} disabled onClick={this.handleFavorite}> <FaHeart  /> </BtnXlFavorite>
						     		</span>
						     		</OverlayTrigger>
								}
								
					     	</Col>
					     	<Col md={2}>
					     		{this.props.isAuthenticated? 
									<OverlayTrigger
									    placement="top"
									    overlay={
									        <Tooltip >
									        	{!this.state.isWatchlist? "Add to watchlist." : "Remove from watchlist."}
									        </Tooltip>
									    }
									>
									<BtnXLWatchlist variant="outline-light" onClick={this.handleWatchlist}> <FaBookmark  /> </BtnXLWatchlist>
									</OverlayTrigger>
									:
									<OverlayTrigger
									    placement="top"
									    overlay={
									        <Tooltip >
									        	Please sign in first.
									        </Tooltip>
									    }
									>
									<span className="d-inline-block">
						     			<BtnXLWatchlist variant="outline-light" style={{pointerEvents: 'none'}} disabled onClick={this.handleWatchlist}> <FaBookmark  /> </BtnXLWatchlist>
						     		</span>
						     		</OverlayTrigger>
								}
					     	</Col>
					     	<Col md={7}>
					     		{this.props.isAuthenticated ?
					     			<React.Fragment>
						     		<StarRatings
	          							rating={this.state.rating}
	          							starRatedColor="red"
	          							changeRating={this.changeRating}
	          							numberOfStars={5}
	          							starDimension="35px"
	        						/>
	        						<OverlayTrigger
									    placement="top"
									    overlay={
									        <Tooltip >
									        	{"Cancel rated."}
									        </Tooltip>
									    }
									>
									<BtnSMMinus variant="outline-light" onClick={this.cancelRated}> <FaMinusCircle  /> </BtnSMMinus>
									</OverlayTrigger>
									</React.Fragment>
									:
									null
								}
        						
					     	</Col>
					      </Row>
					      <Row style={{marginTop: 10}}>
						      <Col>
						      	<p style={{color:"white"}} className="font-weight-normal">IMDB Score: {this.state.movie.imdbRating}</p>
						      </Col>	
						      <Col>
						      	<p style={{color:"white"}} className="font-weight-normal">IMBD Vote: {this.state.movie.imdbVotes}</p>
						      </Col>
						      <Col>
						      	<p style={{color:"white"}} className="font-weight-normal">Runtime: {this.state.movie.Runtime}</p>
						      </Col>
						      <Col>
						      	<p style={{color:"white"}} className="font-weight-normal">Language: {this.state.movie.Language}</p>
						      </Col>
					      </Row>
					      <h2 style={{color:"white"}}>Overview</h2>
					      <p style={{color:"white"}} className="font-weight-normal">{this.state.movie.Plot}</p>
					      <Row>
					      	<Col>
					      		<h2 style={{color:"white"}}>Actors</h2>
					      		<p style={{color:"white"}} className="font-weight-normal">{this.state.movie.Actors}</p>
					      	</Col>
					      	<Col>
					      		<h2 style={{color:"white"}}>Director</h2>
					      		<p style={{color:"white"}} className="font-weight-normal">{this.state.movie.Director}</p>
					      	</Col>
					      </Row>

					    </Col>
					</Row>
				</JumbotronStyle>
			</React.Fragment>
		);
	}
}

